@use 'base/variables' as var;

h1, h2, h3 {
    line-height: 1.2;
}
h1, h2 {
    font-weight: 400;
}
h1 {
    font-size: 4.4rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 2.4rem;
    font-weight: 300;
}
[class$="heading"] {
    margin: 2rem 0;
}
a {
    text-decoration: none;
    color: var.$negro;
}
p {
    font-size: 1.6rem;
    line-height: 1.8;
}