@use 'base/mixins' as mix;
@use 'base/variables' as var;

.header{
    padding: 5rem 0;

    &__grid{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include mix.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo{
        width: 30rem;
        margin-bottom: 3rem;
        @include mix.tablet{
            margin-bottom: 0;
        }
    }
}
